<template>
    <div>
        <button type="button" aria-label="Убрать один товар" @click.prevent="decrement(amount)">
            <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model.number="amount" name="count" @input.number="$emit('update:amount', $event.target.value)">

        <button type="button" aria-label="Добавить один товар" @click.prevent="increment(amount)">
            <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>


<script>
    export default {
        props: ['amount'],
        emits: ['update:amount'],
        methods: {
            increment(amount) {
                this.$emit('update:amount', amount + 1)
            },
            decrement(amount) {
                if(amount <= 1) {
                    amount = 1
                }else {
                    this.$emit('update:amount', amount - 1)
                }
            }
        }
    }
</script>